.nav {
  margin-left: 60%;
  border-left: 1px solid black;
}

.nav a {
  display: block;
  width: 100%;
  color: black;
  line-height: 30px;
  text-align: center;
}

.nav a.active {
  font-weight: bold;
  background-color: #eee;
}

.nav a:hover { background-color: #f7f7f7; }
.nav a.active:hover { background-color: #eee; }

.nav a + a { border-top: 1px solid black; }

@media (min-width: 768px) {
  .nav {
    margin-left: 0;
    border-left: none;
  }

  .nav a {
    display: inline-block;
    width: 33.333%;
    line-height: 50px;
  }

  .nav a + a {
    border-top: none;
    border-left: 1px solid black;
  }
}
.header {
  margin-bottom: 20px;
  border-bottom: 1px solid black;
}

.header a { color: black; }

.header h1 {
  float: left;
  font-size: 24px;
  text-transform: uppercase;
  width: 50%;
  word-spacing: 9999px;
}

@media (min-width: 768px) {
  .header h1 {
    float: none;
    width: 100%;
    margin-bottom: 40px;
    font-size: 36px;
    text-align: center;
    word-spacing: 0;
  }
}
.country {
  padding: 5px;
  text-align: center;
  color: black;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
}

.country h2 {
  padding: 5px;
  letter-spacing: 1px;
  border-bottom: 1px solid #ddd;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
}

.country:hover h2 {
  font-weight: bold;
  background-color: #eee;
}

.flag {
  width: 100%;
  margin: 15px 0;
  border-radius: 4px;
}

@media (min-width: 1000px) {
  .country h2 { font-size: 20px; }
}
.countries .country {
  display: inline-block;
  width: 45%;
  margin: 10px 0 10px 10%;
}

.countries .country:nth-child(2n + 1) { margin-left: 0; }

@media (min-width: 768px) {
  .countries .country {
    width: 30%;
    margin-left: 5%;
  }

  .countries .country:nth-child(2n + 1) { margin-left: 5%; }
  .countries .country:nth-child(3n + 1) { margin-left: 0; }
}

@media (min-width: 1200px) {
  .countries .country {
    width: 22%;
    margin-left: 4%;
  }

  .countries .country:nth-child(2n + 1), .countries .country:nth-child(3n + 1) { margin-left: 4%; }
  .countries .country:nth-child(4n + 1) { margin-left: 0; }
}
.mapContainer {
  height: 200px;
  width: 100%;
}

.mapContainer .map { height: 100%; }

@media (min-width: 600px) {
  .mapContainer { height: 500px; }
}

@media (min-width: 768px) {
  .mapContainer { height: 700px; }
}
.about .about-text {
  margin-bottom: 50px;
  text-align: justify;
}

.about .about-text p { margin-bottom: 20px; }

.about .built-with {
  width: 200px;
  margin: 50px auto; 
}

.about .built-with ul {
  margin-left: 50px;
  list-style-type: disc;
}

.about .built-with li { padding: 7px 0; }

.about .built-with li a {
  color: red;
  text-decoration: underline;
}

@media (min-width: 768px) {
  .about .about-text{
    width: 75%;
    margin: 0 auto 50px auto;
  }
}

@media (min-width: 1000px) {
  .about .about-text { width: 50%; }
}
.country-header {
  padding-bottom: 20px;
  text-align: center;
  line-height: 1.2;
}

.country-header h2 { font-size: 36px; }
.country-header p { font-size: 20px; }
.postcard .city {
  margin-bottom: 10px;
  font-size: 18px;
}

.postcard + .postcard {
  padding-top: 15px;
  margin-top: 15px;
  border-top: 1px solid black;
}

.postcard span {
  display: inline-block;
  width: 100%;
}

.postcard span + span { margin-top: 20px; }
.postcard img { width: 100%; }

.postcard .insta {
  display: block;
  width: 150px;
  color: red;
  font-size: 14px;
}

.postcard .insta:hover { text-decoration: underline; }

@media (min-width: 1000px) {
  .postcard span {
    vertical-align: top;
    width: 50%;
  }

  .postcard img { width: 97%; }
  .postcard span + span { margin-top: 0; }
  .postcard span + span img { float: right; }
}
.not-found p {
  font-size: 40px;
  text-align: center;
}
.footer {
  margin-top: 15px;
  text-align: center;
  border-top: 1px solid black;
}

.footer a {
  color: black;
  text-decoration: underline;
}

.footer p { padding: 10px 0; }

.footer .fa-heart {
  vertical-align: middle;
  color: red;
  font-size: 40px;
  -webkit-animation: beat 1s infinite ease-in-out;
  animation: beat 1s infinite ease-in-out;
}

@-webkit-keyframes beat {
  0% { -webkit-transform: scale(0); transform: scale(0); }
  50% { -webkit-transform: scale(.75); transform: scale(.75); }
  100% { -webkit-transform: scale(0); transform: scale(0); }
}

@keyframes beat {
  0% { -webkit-transform: scale(0); transform: scale(0); }
  50% { -webkit-transform: scale(.75); transform: scale(.75); }
  100% { -webkit-transform: scale(0); transform: scale(0); }
}

.footer .fa-instagram,
.footer .fa-github,
.footer .fa-linkedin {
  margin: 0 25px;
  font-size: 30px;
}

.footer .fa-instagram:hover { color: #f92c01; }
.footer .fa-github:hover { color: #582879; }
.footer .fa-linkedin:hover { color: #0274b3; }
* { box-sizing: border-box; }

body {
  margin: 10px;
  font: normal 16px Helvetica, Arial, sans-serif;
}

a { text-decoration: none; }

.clearfix::before,
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
